import uploadImage from '../../componets/upload-image.vue';
import uniNavBar from '../../componets/uni-nav-bar/uni-nav-bar.vue';
export default {
	components: {
		uploadImage,
		uniNavBar
	},
	data() {
		return {
			textareaAInput: '',
			imageList: [],
		}
	},
	methods: {
		back() {
			if (!this.textareaAInput	 && this.imageList.length < 1) {
				return;
			}
			uni.showModal({
				content: "是否保存为草稿",
				cancelText: "不保存",
				confirmText: "保存",
				success: res => {
					if (res.confirm) {
						console.log('保存')
					} else {
						console.log("不保存")
					}
					uni.navigateBack({
						delta: 1
					})
				}
			})
		},
		textareaAInput(e) {
			this.textareaAValue = e.detail.value
		},
		jumpSuccess() {
			uni.navigateTo({
				url: 'order_comment_success',
			});
		},
		upload(e) {
			this.imageList = e;
		}
	}
}
